.theme-box{
    padding: 5px;

    div{
        border:1px solid white;
        background-origin: 2px;
        cursor: pointer;
        width: 20px;
        height: 20px;
        display: inline-block;
        margin-left:5px;
    }

    #theme-black{
        background-color: black;
        color: white;
    }


    #theme-blue{
        background-color: blue;
        color: white;
    }

    #theme-red{
        background-color: red;
        color: white;
    }

    #theme-green{
        background-color: green;
        color: white;
    }
    #theme-pink{
        background-color: rgb(221, 63, 89);
        color: black;
    }

}


