.social-container {
    padding: 25px 50px;
    /* background-color: black;
    color: white; */
    text-align: center;
  }

  a.social {
    margin: 0 1rem;
    transition: transform 250ms;
    display: inline-block;
  }

  a.social:hover {
    transform: translateY(-4px);
  }

  a.facebook {
    color: #4968ad;
  }
  
  a.twitter {
    color: #49a1eb;
  }

  a.linkedin{
      color : #0077b5;
  }
  
  .Intro {
      font-size: 20px;
      font-family: Arial, Helvetica, sans-serif;
  }